form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 30px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 25px;
  }

textarea {
  background-color: #f9fbfc;
  border: 5px solid #202329;
  border-radius: 5px;
  box-shadow: 0px 5px 50px rgba(30, 35, 40, 0.25);
  color: #1e1e1f;
  font-size: 20px;
  text-align: center;
  padding: 10px;
  height: 100px;
  width: calc(100% - 25px);
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 50px;
  border-radius: 5px;
  border: none;
  font-family: "Montserrat";
}

.TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    background: red;
  }
