.TodoItem{
    background-color: white;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    box-shadow: 0px 5px 50px rgba(30, 35, 40, 0.15);
}

.TodoItem-p{
    margin: 25px 0 25px 25px;
    width: calc(100% - 120px);
    font-size: 20px;
    line-height: 25px;
    font-weight: 400;
}
.TodoItem-p--complete{
    text-decoration: line-through;
}

.Icon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
    font-size: 25px;
    font-weight: bold;
}

.Icon-check{
    position: absolute;
    left: 15px;
}

.Icon-check--active{
    color: green;
}

.Icon-delete{
    position: absolute;
    top:-25px;
    right: 0;
    background-color: yellow;
    border-radius: 50%;
}

.Icon-delete:hover{
    color: red;
    background-color: black;
}