.TodoSearch{
    background-color: white;
    border-radius: 5px;
    border: 5px solid black;
    margin: 0 25px;
    height: 65px;
    width: calc(100%-60px);
    font-size: 25px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: black;
    box-shadow: 0px 5px 50px black;
}

.TodoSearch::placeholder{
    color: grey;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: aqua;
}