.CreateTodoButton{
    background-color: green;
    border-radius: 50%;
    cursor: pointer;
    font-size: 60px;
    position:fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 60px;
    z-index: 1;

    transform: rotate(0);
    transition: .5s ease;
}

.CreateTodoButton:hover{
    transform: rotate(225deg);
    background-color: red;
}